
Template.__checkName("feeds");
Template["feeds"] = new Template("Template.feeds", (function() {
  var view = this;
  return [ HTML.DIV({
    "class": "form-well add-feed"
  }, "\n    ", HTML.Raw("<h3>Add new feed:</h3>"), "\n    ", Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Feeds"),
      id: Spacebars.call("insertFeedForm"),
      type: Spacebars.call("insert"),
      "label-class": Spacebars.call("control-label"),
      "input-col-class": Spacebars.call("controls"),
      template: Spacebars.call("bootstrap3-horizontal")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("quickForm"));
  }), "\n  "), "\n  ", Blaze._TemplateWith(function() {
    return {
      ready: Spacebars.call(view.templateInstance().subscriptionsReady())
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("loader"), function() {
      return [ "\n    ", Blaze.Each(function() {
        return Spacebars.call(view.lookup("feeds"));
      }, function() {
        return [ "\n      ", Spacebars.include(view.lookupTemplate("feed_item")), "\n    " ];
      }), "\n  " ];
    });
  }) ];
}));
